$kaushan: 'Kaushan Script', cursive;

@mixin line {
	content: '';
	display: block;
	margin: 50px auto;
	width: 60px;
	height: 3px;
	background: #f38181; }
@mixin suptitile {
	font-family: $kaushan;
	font-size: 24px;
	font-weight: 400;
	color: #333;
	text-align: center; }
@mixin title {
	font-size: 30px;
	font-weight: 700;
	color: #333;
	text-transform: uppercase;
	text-align: center; }
@mixin text {
	font-size: 15px;
	font-weight: 400;
	color: #999999;
	text-align: center; }



body {
	font-family: 'Montserrat', sans-serif;
	font-size: 15px;
	color: #333333; }

header {
	background: url('../img/background.jpg') no-repeat center top / cover;
	height: 100vh;
	color: #fff;
	.nav {
		display: flex;
		flex-wrap: wrap;
		justify-content: space-around;
		align-items: center;
		padding-top: 27px;
		&__logo {
			a {
				font-size: 30px;
				font-weight: 700;
				color: #fff;
				text-decoration: none; } }

		&__menu {
			display: flex;
			flex-wrap: wrap;
			a {
				display: block;
				font-size: 14px;
				font-weight: 400;
				color: #fff;
				text-transform: uppercase;
				text-decoration: none;
				margin-left: 52px;
				position: relative;
				transition: 0.3s;
				&:hover {
					color: #fce38a; }
				&:after {
					content: '';
					display: block;
					width: 100%;
					height: 3px;
					background-color: #fce38a;
					opacity: 0;
					position: absolute;
					top: 100%;
					left: 0;
					z-index: 1;
					transition: 0.3s; }
				&:hover:after {
					opacity: 1; } } } }
	.intro {
		&__inner {
			text-align: center;
			margin-top: 171px;
			h2 {
				font-family: $kaushan;
				font-size: 72px;
				font-weight: 400; }
			h1 {
