				font-weight: 700;
				text-transform: uppercase;
				line-height: 1;
				max-width: 840px;
				margin: 0 auto;
				&:after {
					@include line;
					background-color: #Fff; } }
			.intro__btn {
				display: inline-block;
				border: 3px solid #fff;
				padding: 12px 30px;
				font-size: 14;
				font-weight: 700;
				color: #fff;
				text-transform: uppercase;
				text-decoration: none;
				transition: 0.3s;
				&:hover {
					background-color: #fff;
					color: #333333; } } }
		.intro__row {
			display: flex;
			justify-content: space-between;
			width: 96%;
			position: absolute;
			bottom: 0;
			.intro__colum {
				border-top: 2px solid #fff;
				width: 23%;
