					margin: 24px 0 27px;
					b {
						font-size: 24px; }
					span {
						font-size: 18px;
						font-weight: 400; } } }
			.intro__active:before {
				content: "";
				display: block;
				position: absolute;
				top: 0;
				left: 0;
				z-index: 1;
				width: 70px;
				height: 3px;
				background-color: #f38181; } } } }
.suptitile {
