					margin-top: 10px;
					cursor: pointer;
					transition: 0.4s;

					.accordion__header {
						border: 1px solid #e5e5e5;
						position: relative;
						img {
							position: absolute;
							top: 50%;
							left: 15px;
							transform: translateY(-50%); }
						h5 {
							padding: 22px 0 22px 60px;
							font-size: 15px;
							font-weight: 400;
							text-transform: uppercase; } }
					.accordion__text {
						display: none;
						padding: 20px;
						font-family: sans-serif;
						font-style: italic;
						font-size: 14px;
						font-weight: 100;
						color: #999999;
						border: 1px solid #e5e5e5;
						height: 129px;
						overflow: auto; }
					.mostly-customized-scrollbar::-webkit-scrollbar {
						width: 5px;
						height: 40px;
						background-color: #f5f5f5 {
    					border-radius: 8%; } }

					.mostly-customized-scrollbar::-webkit-scrollbar-thumb {
    					background: #95e1d3;
    					border-radius: 8%; }

					.accordion__header:after {
						content: "";
						display: block;
						position: absolute;
						width: 14px;
						height: 14px;
						border-top: 3px solid #ccc;
						border-right: 3px solid #ccc;
						top: 50%;
						right: 20px;
						transform: translateY(-50%) rotate(135deg); } }
				&__item.active {
					margin-top: 0;
					.accordion__header:after {
						transform: translateY(-50%) rotate(-45deg); }
					.accordion__text {
						display: block; } } } } } }

.reviews-slider {
