.slider {
	padding: 70px 0;
	position: relative;
	.prev {
		position: absolute;
		left: 0;
		top: 50%;
		transform: translateY(-50%) rotate(-45deg);
		font-size: 0;
		color: transparent;
		width: 16px;
		height: 16px;
		border-left: 3px solid #cccccc;
		border-top: 3px solid #cccccc; }
	.next {
		position: absolute;
		right: 0;
		top: 50%;
		transform: translateY(-50%) rotate(135deg);
		font-size: 0;
		color: transparent;
		width: 16px;
		height: 16px;
		border-left: 3px solid #cccccc;
		border-top: 3px solid #cccccc; }
	.slider__img {
		width: 144px;
